






































import Vue from "vue";
export default Vue.extend({
  name: "custom-menu",
  props: {
    backgroundColor: {
      type: String,
      default: "#20babb"
    },
    color: {
      type: String,
      default: "#fff"
    }
  },
  data() {
    return {
      isActivated: false
    };
  },
  methods: {
    getSubMenu(n: any) {
      let menuPosition = [
        ["0", "-160"],
        ["80", "-138.6"],
        ["138.6", "-80"],
        ["160", "0"]
      ];
      return {
        top: menuPosition[n][0] + "px",
        left: menuPosition[n][1] + "px"
      };
    },
    menuStyle() {
      return {
        backgroundColor: this.backgroundColor,
        color: this.color
      };
    },
    toggleMenu(e: any) {
      this.isActivated = !this.isActivated;
    },
    processCallback(key: any) {
      this.$emit("process", key);
    },
    scrollToSection(section: string) {
      let sectionElement = document.getElementById(section);
      if (sectionElement != null) {
        sectionElement.scrollIntoView({ block: "start", behavior: "smooth" });
        this.isActivated = !this.isActivated;
      } else {
        // console.log(
        //   "The element associated with the menu item is null / could not be found."
        // );
      }
    }
  }
});
